<template>
  <div>
    <div class="container">
      <div class="header">
        <div class="row">
          <div class="col-sm-12 col-md-5 col-lg-3 item item-1 curp" @click.stop.prevent="handleHome">
            <h1>NAS
              <span class="color-theme">|CookBook</span>
            </h1>
            <p class="color-theme">星云菜谱</p>
          </div>
          <div class="col-sm-12 col-md-2 col-lg-3 item item-2 tc curp" @click.stop.prevent="handleHome">
            <span>首页</span>
          </div>
          <div class="col-sm-12 col-md-2 col-lg-3 item item-3 tc curp" @click.stop.prevent="handleCreate">
            <span>上传菜谱</span>
          </div>
          <div class="col-sm-12 col-md-3 col-lg-3 item item-4">
            <form>
              <div class="form-group row">
                <div class="col-xs-11 col-md-10 input">
                  <input type="text" class="form-control" placeholder="请输入菜谱名称" v-model="search">
                </div>
                <label for="staticEmail" class="col-xs-1 col-md-2 col-form-label tr curp" @click.stop.prevent="handleSearch" title="Search">
                  <span class="oi" data-glyph="magnifying-glass" aria-hidden="true"></span>
                </label>
              </div>
            </form>
          </div>
        </div>
      </div>
      <v-toast :show="toastConfig.show" :info="toastConfig.info" @toastHide="toastHide"></v-toast>
      <v-loading v-show="loadingShow"></v-loading>
    </div>
    <div class="container">
      <div class="home">
        <div class="row">
          <div class="empty" v-if="isEmpty">
            <p>没有搜索到
              <span class="color-theme">{{searchName}}</span>相关的菜谱 ==!</p>
            <button type="button" class="btn btn-link btn-lg btn-block color-theme" @click.stop.prevent="handleCreate">
              <span>创建菜谱</span>
            </button>
          </div>
          <div class="item col" v-else>
            <div class="title row">
              <div class="col col-md-2 rec color-theme">
                <p>搜索结果</p>
                <span></span>
              </div>
              <div class="col rec-tit">
                <h5 class="color-theme">{{resultInfo.name}}</h5>
                <p>原料：{{resultInfo.material}}</p>
                <p>调味料：{{resultInfo.flavoring}}</p>
              </div>
            </div>
            <div class="content">
              <p v-for="(item, index) in resultInfo.desc" :key="index">
                <span>{{index+1}}.</span>{{item}}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import bus from "../eventBus.js";
import Header from './header.vue'
import Toast from './toast.vue'
import Loading from './loading.vue'
import NebPay from '../../static/nebpay/nebpay.js'
import { Neb, Account, HttpRequest } from 'nebulas'
// DAPPADDRESS, API
import {
  DAPPADDRESS_TEST,
  DAPPADDRESS_PROD,
  API_TEST,
  API_PROD
} from '../configs'
let DAPPADDRESS = DAPPADDRESS_PROD
let API = API_PROD

export default {
  data() {
    return {
      search: '',
      resultInfo: {},
      searchName: '',
      isEmpty: true,
      toastConfig: {
        info: '',
        show: false
      },
      loadingShow: false
    }
  },
  created() {
    const env = this.$route.query.env
    if (env && env === 'test') {
      DAPPADDRESS = DAPPADDRESS_TEST
      API = API_TEST
    }
    this.nebPay = new NebPay()
    // 直接访问星云链的api
    this.neb = new Neb(new HttpRequest(API))
    this.api = this.neb.api
  },
  mounted() {
    // const self = this
    // bus.$on("searchResult", function(res) {
    //   const { searchName = "", resultInfo = {} } = res;
    //   console.log(searchName, resultInfo);
    //   const {
    //     name = "",
    //     material = "",
    //     flavoring = "",
    //     desc = ""
    //   } = resultInfo;
    //   self.name = name;
    //   self.material = material;
    //   self.flavoring = flavoring;
    //   self.desc = desc;

    //   self.resultInfo = resultInfo;
    //   self.searchName = searchName;
    //   if (resultInfo) {
    //     self.isEmpty = false;
    //   } else {
    //     self.isEmpty = true;
    //   }
    // });
    this.init()
  },
  methods: {
    init() {
      this.searchName = this.$route.params.searchName
      this.resultInfo = this.$route.params.resultInfo
      if (this.resultInfo) {
        this.isEmpty = false
      } else {
        this.isEmpty = true
      }
    },
    handleSearch() {
      if (!this.search) {
        this.toastConfig = {
          info: '请输入您要搜索的菜品名称！',
          show: true
        }
        return false
      }
      this.searchName = this.search
      this.loadingShow = true
      var from = Account.NewAccount().getAddressString()
      var value = '0' // 金额
      var nonce = '0' // 交易序号
      var gasPrice = '1000000' // 手续费价格
      var gasLimit = '2000000' // 手续费限制
      var contract = {
        // 合约
        function: 'get', // 方法名
        args: `["${this.search}"]` // 参数
      }
      // const self = this
      this.neb.api
        .call(from, DAPPADDRESS, value, nonce, gasPrice, gasLimit, contract)
        .then(res => {
          this.loadingShow = false
          if (res.result !== 'null') {
            const resultInfo = JSON.parse(res.result)
            resultInfo.desc = (resultInfo.desc || '').split('<br/>')
            this.resultInfo = resultInfo
            this.isEmpty = false
          } else {
            this.isEmpty = true
          }
        })
        .catch(err => {
          this.loadingShow = false
          this.toastConfig = {
            info: '服务器请求异常，请重试！',
            show: true
          }
          console.log('error:' + err.message)
        })
    },
    handleHome() {
      this.$router.push({ name: 'index' })
    },
    handleCreate() {
      this.$router.push({
        name: 'create',
        params: { searchName: this.search }
      })
    },
    toastHide() {
      this.toastConfig = {
        info: '',
        show: false
      }
    }
  },
  components: {
    Header,
    'v-toast': Toast,
    'v-loading': Loading
  }
}
</script>

<style scoped lang="less">
.title {
  margin-bottom: 2rem;
}
.create {
  padding: 3rem;
}
.form-group {
  margin-bottom: 1.5rem;
}
input {
  border-radius: 0;
  font-size: 0.8rem;
  line-height: 2.5;
}
button {
  text-decoration: none !important;
  font-size: 1rem;
  font-weight: bold;
}
.rec {
  padding-bottom: 2rem;
  span {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: url('../assets/result.png') no-repeat;
    background-size: contain;
    background-position: 0 60%;
  }
}
.rec-tit {
  h5 {
    margin-bottom: 2rem;
  }
  p {
    margin-bottom: 0.5rem;
  }
}
.content {
  background: #ffcc99;
  padding: 2rem;
  min-height: 350px;
  p {
    display: -webkit-flex;
    display: flex;
    span {
      -webkit-flex-shrink: 0;
      flex-shrink: 0;
      min-width: 1.2rem;
      display: inline-block;
    }
  }
}
.empty {
  width: 100%;
  text-align: center;
  padding-top: 160px;
  background: url('../assets/nothing.png') no-repeat;
  background-position: center top;
  background-size: 200px auto;
  button {
    margin-top: 5rem;
  }
  button span {
    width: 4rem;
  }
  button:hover span {
    width: 6rem;
  }
}
@theme: #660000;
.header {
  .item {
    height: 64px;
  }
  line-height: 54px;
  border-bottom: 0.4rem solid @theme;
  margin: 2rem 0;
  * {
    font-size: 0.9rem;
  }
  .col.active span {
    color: @theme;
  }
  .item-1 {
    line-height: initial;
  }
  form {
    padding-top: 10px;
  }
  input {
    border-radius: 0;
    background: #f9f9f9;
    font-size: 0.8rem;
    height: 34px;
    line-height: 34px;
  }
  .item-1 {
    h1 {
      font-family: cursive;
      font-weight: bold;
      span {
        font-size: 1.6rem;
      }
    }
    h1,
    p {
      margin: 0;
      font-size: 1.6rem;
    }
    p {
      font-size: 12px;
      text-indent: 0.2em;
    }
  }
  .item-2,
  .item-3 {
    span {
      cursor: pointer;
      background-image: url('../assets/arrow-down.png');
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 24px auto;
      padding-right: 24px;
    }
    &.active span {
      background-image: url('../assets/arrow-down-active.png');
    }
  }
  .item-4 {
    label {
      padding-left: 0;
      line-height: 24px;
      &:hover span {
        color: @theme;
      }
    }
  }
}
.item {
  margin-bottom: 15px;
}
@media screen and (max-width: 768px) {
  .header {
    margin-top: 20px;
  }
  .item-1 {
    margin-bottom: 10px;
  }
  .item-2,
  .item-3 {
    text-align: left;
    line-height: 40px;
    height: 40px !important;
  }
  .item-4 {
    .input {
      width: 90%;
    }
    label {
      width: 10%;
    }
  }
}
</style>



