<template>
  <div class="toast-wrapper" v-show="showCopy">{{info}}</div>
</template>

<script>
export default {
  data() {
    return {
      showCopy: this.show
    }
  },
  props: {
    info: {
      type: String,
      default: ''
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show(newVal) {
      this.showCopy = newVal
      if (newVal) {
        setTimeout(() => {
          this.showCopy = false
          this.$emit('toastHide')
        }, 3000)
      }
    }
  }
}
</script>

<style scoped>
.toast-wrapper {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  padding: 15px;
  background: #660000;
  color: #fff;
  font-size: 16px;
  width: 100%;
  text-align: center;
  font-weight: bold;
}
</style>



