<template>
  <div>
    <Header/>
    <div class="container">
      <div class="create">
        <div class="row">
          <div class="col">
            <form>
              <div class="form-group row">
                <label class="tr col-sm-12 col-md-3 col-form-label">菜谱名称：</label>
                <div class="col-sm-12  col-md-9">
                  <input type="text" class="form-control" placeholder="请输入菜谱名称" v-model="name">
                </div>
              </div>
              <div class="form-group row">
                <label class="tr col-sm-12 col-md-3 col-form-label">所需原料：</label>
                <div class="col-sm-12  col-md-9">
                  <input type="text" class="form-control" placeholder="请输入所需原料" v-model="material">
                </div>
              </div>
              <div class="form-group row">
                <label class="tr col-sm-12 col-md-3 col-form-label">所需调味料：</label>
                <div class="col-sm-12  col-md-9">
                  <input type="text" class="form-control" placeholder="请输入所需调味料" v-model="flavoring">
                </div>
              </div>
              <div class="steps">
                <div v-for="(item, index) in steps" :key="index" class="form-group row">
                  <label class="tr col-sm-12 col-md-3 col-form-label">烹调步骤{{index+1}}：</label>
                  <div class="col-sm-12  col-md-9">
                    <div class="input-group">
                      <input type="text" class="form-control" :placeholder="`请输入烹调步骤：${index+1}`" v-bind:value="item" :data-index="index" v-on:change="stepChange">
                      <div class="input-group-prepend" :data-index="index" @click.stop.prevent="handleDelete">
                        <p class="input-group-text curp" id="inputGroupPrepend3">
                          <span class="oi" data-glyph="minus"></span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="form-group row">
                <label class="tr col-sm-12 col-md-3 col-form-label">烹调步骤{{step}}：</label>
                <div class="col-sm-12  col-md-9">
                  <div class="input-group">
                    <input type="text" class="form-control" :placeholder="`请输入烹调步骤：${step}`" v-model="desc">
                    <div class="input-group-prepend" @click.stop.prevent="handleAdd">
                      <p class="input-group-text curp" id="inputGroupPrepend3">
                        <span class="oi" data-glyph="plus"></span>
                      </p>
                    </div>
                  </div>
                </div>
              </div>

              <div class="tc">
                <button type="button" class="btn btn-link btn-lg color-theme" @click.stop.prevent="handleSubmit">
                  <span>提交菜谱</span>
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <v-toast :show="toastConfig.show" :info="toastConfig.info" @toastHide="toastHide"></v-toast>
      <v-loading v-show="loadingShow"></v-loading>
    </div>
  </div>
</template>

<script>
import Header from './header.vue'
import Toast from './toast.vue'
import Loading from './loading.vue'
import NebPay from '../../static/nebpay/nebpay.js'
import { Neb, HttpRequest } from 'nebulas'
// DAPPADDRESS, API
import {
  DAPPADDRESS_TEST,
  DAPPADDRESS_PROD,
  API_TEST,
  API_PROD
} from '../configs'
let DAPPADDRESS = DAPPADDRESS_PROD
let API = API_PROD

export default {
  data() {
    return {
      name: '',
      material: '',
      flavoring: '',
      desc: '',
      toastConfig: {
        info: '',
        show: false
      },
      step: 1,
      steps: [],
      loadingShow: false
    }
  },
  created() {
    const env = this.$route.query.env
    if (env && env === 'test') {
      DAPPADDRESS = DAPPADDRESS_TEST
      API = API_TEST
    }
    this.nebPay = new NebPay()
    // 直接访问星云链的api
    this.neb = new Neb(new HttpRequest(API))
    this.api = this.neb.api
  },
  mounted() {
    this.name = this.$route.params.searchName
  },
  methods: {
    handleSubmit() {
      if (!this.name) {
        this.toastConfig = {
          info: '请输入菜品名称！',
          show: true
        }
        return false
      }
      if (!this.material) {
        this.toastConfig = {
          info: '请输入所需原料！',
          show: true
        }
        return false
      }
      if (!this.flavoring) {
        this.toastConfig = {
          info: '请输入所需调味料！',
          show: true
        }
        return false
      }

      if (this.steps.length) {
        for (let i = 0; i < this.steps.length; i++) {
          if (this.steps[i] === '') {
            this.toastConfig = {
              info: `请输入烹调步骤：${i + 1}，或将此步骤删除`,
              show: true
            }
            return false
          }
        }
      } else {
        if (!this.desc) {
          this.toastConfig = {
            info: '请至少输入一个烹调步骤！',
            show: true
          }
          return false
        }
      }

      this.name = this.name.trim()
      this.material = this.material.trim()
      this.flavoring = this.flavoring.trim()
      this.desc = this.desc.trim()

      let steps = [].concat(this.steps)
      if (this.desc) {
        steps.push(this.desc)
      }
      steps = steps.join('<br/>')

      this.toastConfig = {
        info: '正在创建中，请稍后！',
        show: true
      }
      this.loadingShow = true

      var value = '0'
      var callFunction = 'save'
      // name, material, flavoring, desc
      var callArgs = `["${this.name}", "${this.material}", "${
        this.flavoring
      }", "${steps}"]`
      // 使用nebpay的call接口去调用合约,
      this.nebPay.call(DAPPADDRESS, value, callFunction, callArgs, {
        // 设置listener, 处理交易返回信息
        listener: this.cbPush
      })
    },
    handleAdd() {
      if (!this.desc) {
        this.toastConfig = {
          info: `请输入烹调步骤：第${this.step}步！`,
          show: true
        }
        return false
      }
      this.steps.push(this.desc)
      this.desc = ''
      this.step++
      console.log(this.step, this.steps)
    },
    handleDelete(e) {
      const current = e.currentTarget || {}
      const index = current.dataset.index
      this.steps.splice(index, 1)
      this.step--
      console.log(this.step, this.steps)
    },
    stepChange(e) {
      const current = e.currentTarget || {}
      this.steps[current.dataset.index] = current.value
      console.log(this.steps)
    },
    cbPush(resp) {
      var intervalQuery = setInterval(() => {
        this.toastConfig = {
          info: '正在查询创建结果，请稍后！',
          show: true
        }
        this.api
          .getTransactionReceipt({ hash: resp['txhash'] })
          .then(receipt => {
            if (receipt['status'] === 2) {
              console.log('pending.....')
            } else if (receipt['status'] === 1) {
              console.log('交易成功......')
              this.loadingShow = false
              this.toastConfig = {
                info: '菜谱创建成功！',
                show: true
              }
              clearInterval(intervalQuery)
              alert('菜谱创建成功！')
              location.reload()
            } else {
              this.loadingShow = false
              console.log('交易失败......')
              this.toastConfig = {
                info: receipt.execute_result || '创建失败，请稍后再试',
                show: true
              }
              clearInterval(intervalQuery)
            }
          })
          .catch(() => {
            this.loadingShow = false
            this.toastConfig = {
              info: '创建失败，请稍后再试',
              show: true
            }
            clearInterval(intervalQuery)
          })
      }, 5000)
    },
    toastHide() {
      this.toastConfig = {
        info: '',
        show: false
      }
    }
  },
  components: {
    'v-toast': Toast,
    'v-loading': Loading,
    Header
  }
}
</script>

<style scoped lang="less">
@theme: #660000;
.create {
  padding: 2rem;
  padding-right: 6rem;
}
button {
  margin-bottom: 1rem;
}
.form-group {
  margin-bottom: 1.5rem;
  label {
    line-height: 30px;
  }
}
input {
  border-radius: 0;
  font-size: 0.8rem;
  line-height: 2.5;
}
.input-group {
  input {
    border-right: 0;
    &:focus {
      border-right: 1px solid @theme;
    }
  }
  .input-group-text {
    color: @theme;
  }
  .oi {
    font-size: 0.83rem;
    color: @theme;
    margin-right: 0.2rem;
    height: 14px;
  }
}
button {
  padding-left:6rem;
  width: 20rem;
  max-width: 100%;
  span {
    width: 4rem;
  }
  &:hover span {
    width: 6rem;
  }
}
.steps {
  .form-group {
    margin-bottom: 5px;
  }
}
@media screen and (max-width: 768px) {
  .create {
    padding: 0;
    button {
      padding-left:0;
    }
  }
  label {
    text-align: left;
  }
  .form-group {
    margin-bottom: 1rem;
  }
}
</style>



