<template>
  <div class="container">
    <div class="header">
      <div class="row">
        <div class="col-sm-12 col-md-5 col-lg-3 item-1 curp" @click.stop.prevent="handleHome">
          <h1>NAS
            <span class="color-theme">|CookBook</span>
          </h1>
          <p class="color-theme">星云菜谱</p>
        </div>
        <div class="col-sm-12 col-md-2 col-lg-3 item-2 tc curp" v-bind:class="{active:route==='/'}" @click.stop.prevent="handleHome">
          <span>首页</span>
        </div>
        <div class="col-sm-12 col-md-2 col-lg-3 item-3 tc curp" v-bind:class="{active:route==='/create'}" @click.stop.prevent="handleCreate">
          <span>上传菜谱</span>
        </div>
        <div class="col-sm-12 col-md-3 col-lg-3 item-4">
          <form>
            <div class="form-group row">
              <div class="col-xs-11 col-md-10 input">
                <input type="text" class="form-control" placeholder="请输入菜谱名称" v-model="search">
              </div>
              <label for="staticEmail" class="col-xs-1 col-md-2 col-form-label tr curp" @click.stop.prevent="handleSearch" title="Search">
                <span class="oi" data-glyph="magnifying-glass" aria-hidden="true"></span>
              </label>
            </div>
          </form>
        </div>
      </div>
    </div>
    <v-toast :show="toastConfig.show" :info="toastConfig.info" @toastHide="toastHide"></v-toast>
    <v-loading v-show="loadingShow"></v-loading>
  </div>
</template>

<script>
// import bus from "../eventBus.js";
import Toast from './toast.vue'
import Loading from './loading.vue'
import NebPay from '../../static/nebpay/nebpay.js'
import { Neb, Account, HttpRequest } from 'nebulas'
// DAPPADDRESS, API
import {
  DAPPADDRESS_TEST,
  DAPPADDRESS_PROD,
  API_TEST,
  API_PROD
} from '../configs'
let DAPPADDRESS = DAPPADDRESS_PROD
let API = API_PROD

export default {
  data() {
    return {
      route: this.$route.path,
      search: '',
      toastConfig: {
        info: '',
        show: false
      },
      loadingShow: false
    }
  },
  created() {
    const env = this.$route.query.env
    if (env && env === 'test') {
      DAPPADDRESS = DAPPADDRESS_TEST
      API = API_TEST
    }
    this.nebPay = new NebPay()
    // 直接访问星云链的api
    this.neb = new Neb(new HttpRequest(API))
    this.api = this.neb.api
  },
  mounted() {},
  methods: {
    handleSearch() {
      if (!this.search) {
        this.toastConfig = {
          info: '请输入您要搜索的菜品名称！',
          show: true
        }
        return false
      }
      this.loadingShow = true
      var from = Account.NewAccount().getAddressString()
      var value = '0' // 金额
      var nonce = '0' // 交易序号
      var gasPrice = '1000000' // 手续费价格
      var gasLimit = '2000000' // 手续费限制
      var contract = {
        // 合约
        function: 'get', // 方法名
        args: `["${this.search}"]` // 参数
      }
      const self = this
      this.neb.api
        .call(from, DAPPADDRESS, value, nonce, gasPrice, gasLimit, contract)
        .then(res => {
          this.loadingShow = false
          if (res.result !== 'null') {
            const resultInfo = JSON.parse(res.result)
            resultInfo.desc = (resultInfo.desc || '').split('<br/>')
            // if (self.$route.path === "/search-result") {
            //   bus.$emit("searchResult", {
            //     resultInfo,
            //     searchName: self.search
            //   });
            // } else {
            this.$router.push({
              name: 'search-result',
              params: {
                resultInfo: resultInfo,
                searchName: self.search
              }
            })
            // }
          } else {
            this.$router.push({
              name: 'search-result',
              params: {
                resultInfo: null,
                searchName: self.search
              }
            })
          }
        })
        .catch(err => {
          this.loadingShow = false
          this.toastConfig = {
            info: '服务器请求异常，请重试！',
            show: true
          }
          console.log('error:' + err.message)
        })
    },
    handleHome() {
      this.route = '/'
      this.$router.push({ name: 'index' })
    },
    handleCreate() {
      this.route = '/create'
      this.$router.push({
        name: 'create',
        params: { searchName: this.search }
      })
    },
    toastHide() {
      this.toastConfig = {
        info: '',
        show: false
      }
    }
  },
  components: {
    'v-toast': Toast,
    'v-loading': Loading
  }
}
</script>

<style scoped lang="less">
@theme: #660000;
.header {
  line-height: 54px;
  border-bottom: 0.4rem solid @theme;
  margin: 2rem 0;
  * {
    font-size: 0.9rem;
  }
  .active span {
    color: @theme;
  }
  .item-1 {
    line-height: initial;
  }
  form {
    padding-top: 10px;
  }
  input {
    border-radius: 0;
    background: #f9f9f9;
    font-size: 0.8rem;
    height: 34px;
    line-height: 34px;
  }
  .item-1 {
    h1 {
      font-family: cursive;
      font-weight: bold;
      span {
        font-size: 1.6rem;
      }
    }
    h1,
    p {
      margin: 0;
      font-size: 1.6rem;
    }
    p {
      font-size: 12px;
      text-indent: 0.2em;
    }
  }
  .item-2,
  .item-3 {
    span {
      cursor: pointer;
      background-image: url('../assets/arrow-down.png');
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 24px auto;
      padding-right: 24px;
    }
    &.active span {
      background-image: url('../assets/arrow-down-active.png');
    }
  }
  .item-4 {
    label {
      padding-left: 0;
      line-height: 24px;
      &:hover span {
        color: @theme;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    margin-top: 20px;
  }
  .item-1 {
    margin-bottom: 10px;
  }
  .item-2,
  .item-3 {
    text-align: left;
    line-height: 40px;
    height: 40px;
  }
  .item-4 {
    .input {
      width: 90%;
    }
    label {
      width: 10%;
    }
  }
}
</style>



