<template>
  <div>
    <Header/>
    <div class="container">
      <div class="home">
        <div class="row" v-if="!isEmpty">
          <div class="item col">
            <div class="title row">
              <div class="col col-md-2 rec color-theme">
                <p>今日推荐</p>
                <span></span>
              </div>
              <div class="col rec-tit">
                <h5 class="color-theme">{{resultInfo.name}}</h5>
                <p>原料：{{resultInfo.material}}</p>
                <p>调味料：{{resultInfo.flavoring}}</p>
              </div>
            </div>
            <div class="content">
              <p v-for="(item, index) in resultInfo.desc" :key="index">
                <span>{{index+1}}.</span>{{item}}
              </p>
            </div>
          </div>
        </div>
        <div v-else class="empty">数据加载中...</div>

      </div>
      <v-toast :show="toastConfig.show" :info="toastConfig.info" @toastHide="toastHide"></v-toast>
      <v-loading v-show="loadingShow"></v-loading>
    </div>
  </div>
</template>

<script>
import Header from './header.vue'
import Toast from './toast.vue'
import Loading from './loading.vue'
import NebPay from '../../static/nebpay/nebpay.js'
import { Neb, Account, HttpRequest } from 'nebulas'
// DAPPADDRESS, API
import {
  DAPPADDRESS_TEST,
  DAPPADDRESS_PROD,
  API_TEST,
  API_PROD
} from '../configs'
let DAPPADDRESS = DAPPADDRESS_PROD
let API = API_PROD
export default {
  data() {
    return {
      name: '',
      material: '',
      flavoring: '',
      desc: '',
      resultInfo: {},
      isEmpty: true,
      toastConfig: {
        info: '',
        show: false
      },
      loadingShow: true
    }
  },
  created() {
    const env = this.$route.query.env
    if (env && env === 'test') {
      this.env = env
      DAPPADDRESS = DAPPADDRESS_TEST
      API = API_TEST
    }
    this.nebPay = new NebPay()
    // 直接访问星云链的api
    this.neb = new Neb(new HttpRequest(API))
    this.api = this.neb.api
    this.getRecommend()
  },
  mounted() {},
  methods: {
    getTodayMenu() {
      const menus = [
        '松仁玉米',
        '蟹粉狮子头',
        '大煮干丝',
        '文思豆腐',
        '红烧羊肉',
        '糖醋排骨',
        '粉蒸肉',
        '宫保鸡丁',
        '红烧茄子',
        '口水鸡',
        '毛血旺',
        '水煮肉片',
        '跺脚芋头',
        '小炒肉',
        '蒜苗腊肉',
        '锅包肉',
        '小鸡炖蘑菇',
        '地三鲜',
        '木须肉',
        '干炸带鱼',
        '茄盒子',
        '麻婆豆腐',
        '蚂蚁上树',
        '回锅肉',
        '酸辣土豆丝',
        '棒棒鸡',
        '四喜丸子',
        '蜜汁山药',
        '拔丝苹果',
        '油焖大虾'
      ]
      return menus[Math.floor(Math.random() * 11)]
    },
    getRecommend() {
      var from = Account.NewAccount().getAddressString()
      var value = '0' // 金额
      var nonce = '0' // 交易序号
      var gasPrice = '1000000' // 手续费价格
      var gasLimit = '2000000' // 手续费限制
      var contract = {
        // 合约
        function: 'get', // 方法名
        args: `["${this.getTodayMenu()}"]` // 参数
      }
      this.neb.api
        .call(from, DAPPADDRESS, value, nonce, gasPrice, gasLimit, contract)
        .then(res => {
          this.loadingShow = false
          if (res.result !== 'null') {
            const resultInfo = JSON.parse(res.result)
            resultInfo.desc = (resultInfo.desc || '').split('<br/>')
            this.resultInfo = resultInfo
            this.isEmpty = false
          } else {
            this.toastConfig = {
              info: '服务器数据异常，请稍后重试！',
              show: true
            }
          }
        })
        .catch(err => {
          this.loadingShow = false
          this.toastConfig = {
            info: '服务器请求异常，请重试！',
            show: true
          }
          console.log('error:' + err.message)
        })
    },
    toastHide() {
      this.toastConfig = {
        info: '',
        show: false
      }
    },
    loadingHide() {
      this.loadingConfig = {
        show: false
      }
    }
  },
  components: {
    'v-toast': Toast,
    'v-loading': Loading,
    Header
  }
}
</script>

<style scoped lang="less">
.title {
  margin-bottom: 2rem;
}
.create {
  padding: 3rem;
}
.form-group {
  margin-bottom: 1.5rem;
}
.item {
  margin-bottom: 15px;
}
input {
  border-radius: 0;
  font-size: 0.8rem;
  line-height: 2.5;
}
button {
  text-decoration: none !important;
  font-size: 1rem;
  font-weight: bold;
}
.rec {
  padding-bottom: 2rem;
  span {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: url('../assets/rec.png') no-repeat;
    background-size: contain;
    background-position: 0 60%;
  }
}
.rec-tit {
  h5 {
    margin-bottom: 2rem;
  }
  p {
    margin-bottom: 0.5rem;
  }
}
.content {
  background: #ffcc99;
  padding: 2rem;
  min-height: 350px;
  p {
    display: -webkit-flex;
    display: flex;
    span {
      -webkit-flex-shrink: 0;
      flex-shrink: 0;
      min-width: 1.2rem;
      display: inline-block;
    }
  }
}
</style>



